@import "../../scss/mixins";
@import "../../scss/variables";

.tableWrp {
  box-sizing: border-box;
  padding: 18px;
  border-radius: 15px;
  background: $mainBgColor;
  margin-bottom: 28px;
}
.table {
  border-collapse: collapse;
  width: 100%;
}
.thead {
}

.trHead {
  &:nth-child(even) {
    background-color: rgba(133, 170, 159, 0.1);
  }
}

.thHead {
  position: relative;
  outline: none;
  box-sizing: border-box;
  text-align: left;
  background-color: rgba(133, 170, 159, 0.1);
  border: 1px solid #dbdbdb;
  border-top: 0px solid rgba(133, 170, 159, 0.1);
  padding: 22px;
  @include font-size(20, 500);
  color: $darckTextColor;
  &:first-of-type {
    border-left: 1px solid transparent;
    border-top-left-radius: 8px;
    &::before {
      position: absolute;
      content: "";
      background-repeat: no-repeat;
      background-image: url(../../assets/img/united.png);
      right: 20px;
      top: 20px;
      width: 32px;
      height: 32px;
    }
  }
  &:nth-child(2) {
    &::before {
      position: absolute;
      content: "";
      background-repeat: no-repeat;
      background-image: url(../../assets/img/ukraine.png);
      right: 20px;
      top: 20px;
      width: 32px;
      height: 32px;
    }
  }
  &:last-of-type {
    border-right: 1px solid transparent;
    border-top-right-radius: 8px;
  }
}

.tBody {
  text-align: left;
}

.trBody {
  background: #fcfcfc;
}

.tdBody {
  box-sizing: border-box;
  border: 1px solid #ddd;
  padding: 22px;
  @include font-size(20, 500);
  color: $darckTextColor;
  &:first-of-type {
    border-left: 1px solid transparent;
  }
  &:last-of-type {
    border-right: 1px solid transparent;
  }
  &:last-of-type {
    text-align: center;
  }
}

.actionBtn {
  text-align: center;
  background-color: transparent;
  color: $darckTextColor;
  @include font-size(22, 600);
  &:hover,
  &:focus,
  &:active {
    color: $mainAccentColor;
  }
}

.addToDictionaryBtn {
  text-align: center;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background-color: transparent;
  color: $darckTextColor;
  @include font-size(16, 500);
  &:hover,
  &:focus,
  &:active {
    color: $mainAccentColor;
  }
}
